<template>
	<view id="detail">
		<movie-detail :detailMessage="detailMessage"></movie-detail>
		
		<view class="space">
			<span>资源链接</span>
		</view>
		
		<resource_link :movieUrl="movieUrl" @entryPlay="entryPlay"></resource_link>
	</view>
</template>

<script>
	import { getMovieDetail,getMovieUrl } from '../../service/detail.js'
	import movieDetail from '../../components/itemDetail/itemDetail.vue'
	import resource_link from '../../components/resource_link/resource_link.vue'
	export default {
		components:{ movieDetail,resource_link },
		data() {
			return {
				detailMessage:{},
				movieUrl:[]
			}
		},
		methods: {
			entryPlay(value){
				const {num,url} = value
				console.log(num,url)
				uni.navigateTo({
					url:`../play/play?num=${num}&url=${url}`
				})
			}
		},
		onLoad(option) {
			getMovieDetail(option.name).then(res => {
				this.$data.detailMessage = res
			//	console.log(this.$data.detailMessage)
			})
			getMovieUrl(option.name).then(res => {
				this.$data.movieUrl = res
			  console.log(res)
			})
		}
	}
</script>

<style lang="scss" scoped>
#detail{
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	height: 100%;
	width: 100%;
	padding: 0 10px;
	box-sizing: border-box;
	.space{
		width:100%;
	    height: 70rpx;
		line-height: 70rpx;
		font-size: 28rpx;
		font-weight: bold;
		border-top: 2px solid #ccc;
		background-color:#f4f4f4;
		span{
			display: block;
			height: 100%;
			width: 150rpx;
			background-color: #fff;
			text-align: center;
		}
	}
}
